import React from 'react';
const List = (props) => {
    let size = props.size ? props.size : 16;
    let left = props.left ? props.left : 0;
    let top = props.top ? props.top : 0;
    return (
        <div style={{display:"inline-block",transform:"translate("+left+"px,"+top+"px)"}}>
            <svg width={size} height={size} viewBox="0 0 16 16" className="bi bi-list" fill="currentColor"
                 xmlns="http://www.w3.org/2000/svg" id="listIcon">
                <path fillRule="evenodd"
                      d="M2.5 11.5A.5.5 0 0 1 3 11h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 7h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 3h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
            </svg>
        </div>
    )
};

export default List;
